.cruises {
padding: 95px 15px 120px 15px;

@include vp-1023 {
  padding: 100px 19px 100px 19px;
}

@include vp-767 {
  padding: 65px 15px 65px 15px;
}

}

.cruises__title {
  font-weight: 600;
font-size: 28px;
line-height: 42px;
margin: 0 auto 60px auto;
max-width: 800px;
text-align: center;

@include vp-1023 {
  font-size: 26px;
line-height: 39px;
}

@include vp-767 {
  font-size: 24px;
line-height: 36px;
}
}

.cruises__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  gap: 30px;
  display: grid;
  grid-template-columns: repeat(4, minmax(170px, 1fr));
  grid-auto-rows: 1fr;
  justify-content: center;

  @include vp-1023 {
    grid-template-columns: repeat(2, minmax(300px, 1fr));
  }

  @include vp-767 {
    grid-template-columns: repeat(1, minmax(250px, 380px));
  }
}

.cruises__item {
  position: relative;

  &:hover .cruises-card__popup,
  &:focus-within .cruises-card__popup {
    display: flex;
    visibility:visible;
    flex-grow: 1;
  }

  &:hover,
  &:focus-within {
    visibility: hidden;
  }
}
